<template>
  <div class="class-detail-com">
    <div class="class-album">
      <ClassAlbum :poster="poster" />
    </div>
    <div class="class-members">
      <ClassMember class="member" v-for="(m, index) in members" :info="m" :key="m.id" />
    </div>
  </div>
</template>

<script>
import ClassMember from "@/components/schoolMate/ClassMember.vue";
import ClassAlbum from "@/components/schoolMate/ClassAlbum.vue";

export default {
  components: {
    ClassMember,
    ClassAlbum
  },
  created() {
    this.saveClassID(this.$route);
    this.getPoster();
    this.getAllByClassID();
  },
  beforeRouteUpdate(to, from, next) {
    this.saveClassID(to);
    this.getAllByClassID();
    // 路有变化->请求数据,请求数据mounted中也要请求一次，因为首次载入这个钩子不会执行
    this.getPoster();
    next();
  },
  data() {
    return {
      poster: {},
      members:[]
    };
  },
  methods: {
    saveClassID(route) {
      const id = route.params.classID;
      this.$store.commit({
        type: "set_classID",
        classID: parseInt(id)
      });
    },
    getPoster() {
      const id = this.$store.state.classID;
      this.$api
        .getPoster({
          classID: id
        })
        .then(resp => {
          this.poster = resp.data.data;
        });
    },
    getAllByClassID() {
      const id = this.$store.state.classID;
      this.$api
        .getClassMemberInfo({
          classID: id
        })
        .then(resp => {
          this.members = resp.data.data;
        });
    }
  }
};
</script>

<style scoped lang="scss">
.class-detail-com {
  .class-album {
  }
  .class-members {
    .member {
      float: left;
      margin-top: 0.1rem;
      margin-left: 0.1rem;
    }
  }
}
</style>


